body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#container{
    width: 100%;
    height: 100%;
}
#home-background{
    background-image: url(/static/media/bg.879ee14f.jpeg);
    background-size: cover;
    background-position: center center;
    position: fixed;
    width: 100%;
    height: 100%;
    -webkit-filter: blur(5px);
            filter: blur(5px);
    z-index: -2;
}
#div-title{
    width: 100%;
    position: absolute;
    top: 20%;
}
h1{
    color: #fff;
    margin: 0;
    padding: 0;
}
#div-links{
    width: 100%;
    position: absolute;
    bottom: 25%;
}
#ul-option{
    display: -webkit-flex;
    display: flex;
    padding: 0;
    margin: 0;
}
.li-option{
    -webkit-flex: 1 1;
            flex: 1 1;
    list-style-type: none;
}
.a-option{
    padding-bottom: 5px;
	border-bottom: 1px solid #fff;
}
#div-icp{
    position: fixed;
    width: 100%;
    bottom: 5px;
    text-align: center;
}
#a-icp{
    font-size: small;
    color: grey;
}
a:hover,a:visited,a:link,a:active{
    text-decoration: none;
    color: #fff;
}
#wrapper-background{
    background-image: url(/static/media/bg.879ee14f.jpeg);
    background-size: cover;
    background-position: center center;
    position: fixed;
    width: 100%;
    height: 100%;
    -webkit-filter: blur(10px);
            filter: blur(10px);
    z-index: -2;
}
#div-top{
    position: fixed;
    width: 100%;
    background: #282c34;
    z-index: 2;
}
#div-top-relative{
    position: relative;
    padding: 0.5rem;
    height: 2rem;
}
#btn-menu{
    left: 1rem;
}
#title{
    color: white;
    font-size: 1.3rem;
}
#btn-user{
    right: 1rem;
}
#div-wrapped{
    position: relative;
    top: 3rem;
    background: #ffffff;
    opacity: 0.5;
}
#div-menu{
    margin: 0.2rem;
    position: fixed;
    top: 3rem;
    z-index: 2;
    background: #282c34;
}
#div-user{
    margin: 0.2rem;
    position: fixed;
    top: 3rem;
    right: 0;
    z-index: 2;
    background: #282c34;
}
.wrapper-btn{
    position: absolute;
    background: #282c34;
    width: 3rem;
    height: 2rem;
    padding: 0;
    border: 0;
    color: white;
}
.wrapper-ul{
    margin: 0;
    padding: 0;
}
.wrapper-li{
    list-style: none;
    color: white;
    padding: 0.5rem 1.2rem 0.5rem 1.2rem;
    font-size: 1.3rem;
}
@font-face {font-family: "iconfont";
  src: url(/static/media/iconfont.e0634f80.eot); /* IE9 */
  src: url(/static/media/iconfont.e0634f80.eot#iefix) format('embedded-opentype'), 
  url('data:application/x-font-woff2;charset=utf-8;base64,') format('woff2'),
  url(/static/media/iconfont.dc40c92c.woff) format('woff'),
  url(/static/media/iconfont.d752e7a5.ttf) format('truetype'), 
  url(/static/media/iconfont.cd348350.svg#iconfont) format('svg'); /* iOS 4.1- */
}

.iconfont {
  font-family: "iconfont" !important;
  font-size: 1.5rem;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

