#wrapper-background{
    background-image: url("../../static/images/bg.jpeg");
    background-size: cover;
    background-position: center center;
    position: fixed;
    width: 100%;
    height: 100%;
    filter: blur(10px);
    z-index: -2;
}
#div-top{
    position: fixed;
    width: 100%;
    background: #282c34;
    z-index: 2;
}
#div-top-relative{
    position: relative;
    padding: 0.5rem;
    height: 2rem;
}
#btn-menu{
    left: 1rem;
}
#title{
    color: white;
    font-size: 1.3rem;
}
#btn-user{
    right: 1rem;
}
#div-wrapped{
    position: relative;
    top: 3rem;
    background: #ffffff;
    opacity: 0.5;
}
#div-menu{
    margin: 0.2rem;
    position: fixed;
    top: 3rem;
    z-index: 2;
    background: #282c34;
}
#div-user{
    margin: 0.2rem;
    position: fixed;
    top: 3rem;
    right: 0;
    z-index: 2;
    background: #282c34;
}
.wrapper-btn{
    position: absolute;
    background: #282c34;
    width: 3rem;
    height: 2rem;
    padding: 0;
    border: 0;
    color: white;
}
.wrapper-ul{
    margin: 0;
    padding: 0;
}
.wrapper-li{
    list-style: none;
    color: white;
    padding: 0.5rem 1.2rem 0.5rem 1.2rem;
    font-size: 1.3rem;
}